import { Component, OnInit } from "@angular/core";
import { FormGroup, FormControl } from "@angular/forms";
import { PostsService } from "../posts.service";
import { ActivatedRoute, Route, Router } from "@angular/router";

@Component({
  selector: "app-add-notes",
  templateUrl: "./add-notes.component.html",
  styleUrls: ["./add-notes.component.scss"]
})
export class AddNotesComponent implements OnInit {
  priorities: any = ["Urgent", "Major", "Moderate", "Low"];

  hasUpdateNote: boolean = false;

  updateNote: {} = {};

  addNoteForm = new FormGroup({
    title: new FormControl(""),
    description: new FormControl(""),
    priority: new FormControl("")
  });

  constructor(
    public postService: PostsService,
    private route: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {
    this.route.paramMap.subscribe(res => {
      const note = res["params"];
      this.updateNote = note;
      this.hasUpdateNote = Object.keys(this.updateNote).length !== 0;
      if (this.hasUpdateNote) {
        this.patch();
      }
    });
  }

  patch() {
    this.addNoteForm.patchValue({
      title: this.updateNote["title"],
      description: this.updateNote["description"],
      priority: +this.updateNote["priority"]
    });
  }

  onSubmit() {
    const data = this.addNoteForm.value;
    if (this.hasUpdateNote) {
      const id = +this.updateNote["id"];
      this.postService.updateNote(data, id).subscribe(
        res => {
          this.router.navigate(["/user/dashboard"]);
        },
        err => console.log(err)
      );
    } else {
      this.postService.addNote(data).subscribe(
        res => {
          this.router.navigate(["/user/dashboard"]);
        },
        err => console.log(err)
      );
    }
  }
}
