import { Injectable, Output, EventEmitter } from "@angular/core";
import { HttpClient, HttpErrorResponse } from "@angular/common/http";
import { Post } from "./post";
import { Observable, throwError, BehaviorSubject } from "rxjs";
import { HttpHeaders } from "@angular/common/http";
import { tap } from "rxjs/operators";

const httpOptions = {
  headers: new HttpHeaders({
    "Content-Type": "application/json"
  })
};

@Injectable({
  providedIn: "root"
})
export class PostsService {
  public isLoggedIn = new BehaviorSubject<boolean>(false);

  loginStatus = this.isLoggedIn.asObservable();

  baseUrl: string = "http://127.0.0.1:8000/api/posts/";

  loginUrl: string = "http://127.0.0.1:8000/api/auth/login";
  registerUrl: string = "http://127.0.0.1:8000/api/auth/register";
  userUrl: string = "http://127.0.0.1:8000/api/auth/user";
  token: string;
  data: [];

  constructor(private http: HttpClient) {}

  getUserPosts(): Observable<Post[]> {
    return this.http.get<Post[]>(this.baseUrl, httpOptions).pipe(
      tap(res => {
        const data = res;
        data.forEach(element => {
          switch (element["priority"]) {
            case 0:
              element["priority"] = "Urgent";
              break;
            case 1:
              element["priority"] = "Major";
              break;
            case 2:
              element["priority"] = "Moderate";
              break;
            case 3:
              element["priority"] = "Low";
              break;
            default:
              element["priority"] = "Moderate";
          }
        });
        return data;
      })
    );
  }

  addNote(note: any): Observable<any> {
    return this.http.post<any>(this.baseUrl, note, httpOptions);
  }
  updateNote(note: any, ID: number): Observable<any> {
    const id = ID;

    return this.http.put<any>(`${this.baseUrl}${id}/`, httpOptions);
  }

  login(user) {
    return this.http
      .post<{ access_token: string }>(this.loginUrl, user, httpOptions)
      .pipe(
        tap(res => {
          const tok = res["token"];
          localStorage.setItem("access_token", tok);
          this.isLoggedIn.next(true);
        })
      );
  }

  register(user) {
    return this.http.post<{ access_token: string }>(
      this.registerUrl,
      user,
      httpOptions
    );
  }
  logout() {
    localStorage.removeItem("access_token");
    this.isLoggedIn.next(false);
  }
  checkloggedIn() {
    if (localStorage.getItem("access_token") !== null) {
      this.isLoggedIn.next(true);
    }
  }

  getUser() {
    return this.http.get(this.userUrl, httpOptions);
  }

  deleteNote(id) {
    return this.http.delete(`${this.baseUrl}${id}`, httpOptions);
  }
}
